body {
    font-family: Arial, sans-serif;
    background: #f3f3f3;
    margin: 0;
    padding: 0;
}

/* Navbar */
.navbar {
    background: #333;
    color: white;
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar a,
.navbar button {
    color: white;
    margin-left: 15px;
    text-decoration: none;
    border: none;
    background: none;
    cursor: pointer;
}

/* Auth */
.auth-container {
    max-width: 400px;
    background: white;
    margin: 50px auto;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
}

.auth-container input,
.auth-container button {
    width: 100%;
    margin: 10px 0;
    padding: 10px;
}

/* Notes */
.note-form {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: auto;
}

.note-form input,
.note-form textarea,
.note-form button {
    margin: 5px 0;
    padding: 10px;
}

.notes-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 15px;
    padding: 20px;
}

.note-card {
    background: white;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.note-card button {
    background: crimson;
    color: white;
    border: none;
    padding: 8px 10px;
    border-radius: 5px;
    cursor: pointer;
}

/* Responsive */
@media (max-width: 600px) {
    .navbar h2 {
        font-size: 18px;
    }

    .notes-grid {
        grid-template-columns: 1fr;
    }
}